import React from 'react';
import { MDBCol, MDBContainer, MDBRow, MDBCard, MDBCardText, MDBCardBody, MDBCardImage, MDBBtn, MDBTypography} from 'mdb-react-ui-kit';
import { Container, Row, Col } from 'react-bootstrap';

export default function ProfileStatistics() {
  return (
    <div className="home">
      <div style={{ minHeight: '20px', backgroundColor: '#a0e4fc'}} className="mb-0 w-100"></div>
      <Container fluid style={{backgroundColor: 'white', marginTop: '-20px', borderTopLeftRadius: '30px', borderTopRightRadius: '30px' }}>
        <MDBContainer className="py-5 h-100 d-flex justify-content-center align-items-center">
          {/* Use a single MDBRow to contain all MDBCol elements */}
          <MDBRow className="justify-content-center align-items-center h-100">
            {/* Each MDBCol represents one card, arranged vertically within the MDBRow */}
            <MDBCol md="12" xl="4" className="mb-4"> {/* Add margin-bottom for spacing between cards */}
              {/* First Card */}
              <MDBCard style={{ borderRadius: '15px' }}>
              <MDBCardBody className="text-center">
                  <div className="mt-3 mb-4">
                    <MDBCardImage src="https://mdbcdn.b-cdn.net/img/Photos/new-templates/bootstrap-chat/ava1-bg.webp"
                      className="rounded-circle" fluid style={{ width: '100px' }} />
                  </div>
                  <MDBTypography tag="h4">Giorgia Lanzaretti</MDBTypography>
                  <MDBCardText className="text-muted mb-4">
                      @Laureanda @Grafica 
                  </MDBCardText>
                  <MDBBtn rounded size="lg">
                    Contatta
                  </MDBBtn>
                </MDBCardBody>
              </MDBCard>
            </MDBCol>
            <MDBCol md="12" xl="4" className="mb-4"> {/* Add margin-bottom for spacing between cards */}
              {/* Second Card */}
              <MDBCard style={{ borderRadius: '15px' }}>
              <MDBCardBody className="text-center">
                  <div className="mt-3 mb-4">
                    <MDBCardImage src=""
                      className="rounded-circle" fluid style={{ width: '100px' }} />
                  </div>
                  <MDBTypography tag="h4">Dott.ssa Zotti Francesca</MDBTypography>
                  <MDBCardText className="text-muted mb-4">
                    @Relatore
                  </MDBCardText>
                  <MDBBtn rounded size="lg">
                    Contatta
                  </MDBBtn>
                </MDBCardBody>
              </MDBCard>
            </MDBCol>
            <MDBCol md="12" xl="4" className="mb-4"> {/* Add margin-bottom for spacing between cards */}
              {/* Second Card */}
              <MDBCard style={{ borderRadius: '15px' }}>
              <MDBCardBody className="text-center">
                  <div className="mt-3 mb-4">
                    <MDBCardImage src="https://mdbcdn.b-cdn.net/img/Photos/new-templates/bootstrap-chat/ava3-bg.webp"
                      className="rounded-circle" fluid style={{ width: '100px' }} />
                  </div>
                  <MDBTypography tag="h4">Dott. Montagna Pietro</MDBTypography>
                  <MDBCardText className="text-muted mb-4">
                    @Correlatore @Sviluppo
                  </MDBCardText>
                  <MDBBtn rounded size="lg">
                    Contatta
                  </MDBBtn>
                </MDBCardBody>
              </MDBCard>
            </MDBCol>
          </MDBRow>
        </MDBContainer>
      </Container>
      </div>
  );
}
