import '../../assets/EveryClinicalCaseStyles.css';
import PatientsData from '../../data/PatientsData';
import ColorDecider from '../../utils/ColorDecider';
import React, { useState, useEffect, useRef} from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFire, faExpandAlt, faDownLeftAndUpRightToCenter} from '@fortawesome/free-solid-svg-icons';
import { Container, Row, Col} from 'react-bootstrap';
import Card from 'react-bootstrap/Card';
import { useNavigate } from 'react-router-dom';
import { aliasesInjector } from '../../utils/AliasesInjector';
import { useParams } from 'react-router-dom';

const CaseCard = ({ data, onClick, isActive }) => {
  const  cardTitleRef = useRef();
  const navigate = useNavigate();
  const [backgroundColor, setBackgroundColor] = useState('transparent');
  
  // Render images if available
  const renderImages = (images) => {
    return (
      <Row style={{alignItems: 'center', justifyContent: 'center', margin: 'auto'}}>
        {images.map((imgSrc, index) => (
          <Col xs={12} md={6} lg={3} key={index} style={{marginTop: '1em'}} className="mb-2">
            <Card>
              <Card.Img variant="top" src={process.env.PUBLIC_URL + imgSrc} alt={`Image ${index}`} />
            </Card>
          </Col>
        ))}
      </Row>
    );
  };
  
  

  // Utility function to convert hex to RGBA
  function hexToRGBA(hex, opacity) {
      let r = parseInt(hex.slice(1, 3), 16),
          g = parseInt(hex.slice(3, 5), 16),
          b = parseInt(hex.slice(5, 7), 16);

      if (opacity > 1) opacity = opacity / 100;
      
      return `rgba(${r}, ${g}, ${b}, ${opacity})`;
  }

  useEffect(() => {
      const newBackgroundColor = hexToRGBA(ColorDecider(data.category), 0.2);
      const timeoutId = setTimeout(() => setBackgroundColor(newBackgroundColor), 300);

      return () => clearTimeout(timeoutId);
  }, [data.category]);

  useEffect(() => {
    let timeoutId;
  
    if (isActive && cardTitleRef.current) {
      timeoutId = setTimeout(() => {
        cardTitleRef.current.scrollIntoView({ behavior: 'smooth'});
      }, 10); // Delay it to the next tick of the event loop
    }
  
    return () => {
      if (timeoutId) {
        clearTimeout(timeoutId); // Clear the timeout if the card becomes inactive before the timeout fires
      }
    };
  }, [isActive]);
  
  
  
  
  return (
    <Card 
        className='case-card' 
        style={{ backgroundColor: backgroundColor, transition: 'background-color 0.3s ease' }}
        
    >
      <Card.Body className="text-center case-card-body">
        <p 
          className='case-card-title'
          ref={cardTitleRef} 
          tabIndex="-1"
        >
          {data.title}
        </p>
        <div className='case-card-description' style={{ display: !isActive ? 'block' : 'none' }}>
          {aliasesInjector(data.card.description, navigate)}
        </div>

        <div className='case-card-content' style={{ display: isActive ? 'block' : 'none' }}>
          {data.flashcard.anamnesis?.text && (
            <>
              <p className='case-card-label'>Anamnesi</p>
              <div className='case-card-extend-data'>
                {aliasesInjector(data.flashcard.anamnesis.text, navigate)}
                {data.flashcard.anamnesis?.image && renderImages(data.flashcard.anamnesis.image)}
              </div>
            </>
          )}

          {data.flashcard.examination?.text && (
                  <>
                    <p className='case-card-label'>Esame obiettivo</p>
                    <div className='case-card-extend-data'>
                      {aliasesInjector(data.flashcard.examination.text, navigate)}
                      {data.flashcard.examination?.image && renderImages(data.flashcard.examination.image)}
                    </div>
                  </>
          )}

          {data.flashcard.diagnostic_phase?.text && (                  
                  <>
                    <p className='case-card-label'>Indagini diagnostiche</p>
                    <div className='case-card-extend-data'>
                      {aliasesInjector(data.flashcard.diagnostic_phase.text, navigate)}
                      {data.flashcard.diagnostic_phase?.image && renderImages(data.flashcard.diagnostic_phase.image)}
                    </div>
                  </>
          )}

          {data.flashcard.diagnosis?.text && (
                  <>
                    <p className='case-card-label'>Diagnosi</p>
                    <div className='case-card-extend-data'>
                      {aliasesInjector(data.flashcard.diagnosis.text, navigate)}
                      {data.flashcard.diagnosis?.image && renderImages(data.flashcard.diagnosis.image)}
                    </div>
                  </>
                  )}

          {data.flashcard.treatment?.text && (
                  <>
                    <p className='case-card-label'>Terapia</p>
                    <div className='case-card-extend-data'>
                      {aliasesInjector(data.flashcard.treatment.text, navigate)}
                      {data.flashcard.treatment?.image && renderImages(data.flashcard.treatment.image)}
                    </div>
                  </>
            )}

        </div>
      </Card.Body>

      <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', width: '100%'}}>
        <p className='case-card-id'>Caso numero: {data.id}</p>
        <FontAwesomeIcon
          onClick={onClick}
          className='casi-clinici-icon'
          icon={isActive ? faDownLeftAndUpRightToCenter : faExpandAlt}
          style={{
              color: 'black',
              fontSize: `${isActive ? '1.7em' : '1em'}`, // Use template literal to set the fontSize conditionally
              cursor: 'pointer',
              marginRight: '1em'
          }}
        />
      </div>
    </Card>
  );
};

function EveryClinicalCase() {

  const { activeID } = useParams();
  const initialActiveIndex = activeID && !isNaN(activeID) ? parseInt(activeID, 10) : null;

  const [activeCardIndex, setActiveCardIndex] = useState(initialActiveIndex);

  function onCardClick(cardIndex) {
    if (activeCardIndex === cardIndex) {
      setActiveCardIndex(null);
    } else {
      setActiveCardIndex(cardIndex);
    }
  }

  
  //  layout
  return (
    <div className="home">
      <div style={{ minHeight: '20px', backgroundColor: '#a0e4fc'}} className="mb-0 w-100"></div>
      <Container fluid style={{backgroundColor: 'white', marginTop: '-20px', borderTopLeftRadius: '30px', borderTopRightRadius: '30px' }}>
          {/* Hot cases */}
          <Row className="mb-0 row-fade"> 
            <p className='casi-clinici-text'>Casi clinici <FontAwesomeIcon className='casi-clinici-icon' icon={faFire}  style={{color: 'red'}} /></p>
          </Row>
          <Row className="mb-0 align-items-stretch hot-cases-row" style={{ minHeight: '200px' }}> {/* Use align-items-stretch to make cols the same height */}
            <Col lg={12}>
              <Row>
                {PatientsData.map((item, index) => {
                  const isActive = activeCardIndex === index;
                  const colSize = isActive ? 12 : 6; // Full width for active card, else half width

                  return (
                    <Col xs={colSize} sm={colSize} md={isActive ? colSize : 4} lg={isActive ? colSize : 4} xl={isActive ? colSize : 2} className="mb-4 d-flex" key={index}>
                      <CaseCard
                        data={item} 
                        className={`w-100`}
                        isActive={isActive}
                        onClick={() => onCardClick(index)}
                      />
                    </Col>
                  );
                })}
              </Row>
            </Col>
          </Row>
      </Container>
    </div>
  );
  
}

export default EveryClinicalCase;
