import React from 'react';
import InsightsData from '../data/InsightsData';

export const aliasesInjector = (text, navigate) => {
  // Create a map for quick alias lookup
  const aliasMap = new Map();
  InsightsData.forEach(card => {
    card.aliases.forEach(alias => {
      aliasMap.set(alias.toLowerCase(), card.id);
    });
  });

  // Function to find an insight ID by alias
  function findInsightIdByAlias(alias) {
    return aliasMap.get(alias.toLowerCase());
  }

  // Function to replace aliases in text
  function replaceAliasesInText(text) {
    let replacedText = [];
    let currentSegment = '';

    for (let i = 0; i < text.length; i++) {
      currentSegment += text[i];
      const lowerSegment = currentSegment.toLowerCase();

      let foundMatch = false;
      for (const alias of aliasMap.keys()) {
        if (lowerSegment.endsWith(alias)) {
          const insightId = findInsightIdByAlias(alias);
          const nonMatchPart = currentSegment.slice(0, -alias.length);

          if (nonMatchPart) {
            replacedText.push(nonMatchPart);
          }
          replacedText.push(
            <span 
              key={`${alias}-${i}`}
              className="link-like"
              onClick={() => navigate('/insight/' + insightId)}
            >
              <strong>{alias}</strong>
            </span>
          );

          currentSegment = '';
          foundMatch = true;
          break;
        }
      }

      if (!foundMatch && i === text.length - 1) {
        replacedText.push(currentSegment);
      }

      if (text[i] === '\n') {
        if (currentSegment) {
          replacedText.push(currentSegment);
          currentSegment = '';
        }
        replacedText.push(<br key={`br-${i}`} />);
        continue;
      }
  
    }

    return replacedText;
  }

  return <>{replaceAliasesInText(text)}</>;
};
