import '../../assets/EveryQuizStyles.css';
import QuizData from '../../data/QuizData';
import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSquarePollHorizontal, faPlay } from '@fortawesome/free-solid-svg-icons';
import { Container, Row, Col } from 'react-bootstrap';
import Card from 'react-bootstrap/Card';
import { useNavigate } from 'react-router-dom';


//definisco il componente della carta approfondimento
const QuizCard = ({data}) => {
  return (
    <Card>
      {data.context.text}
    </Card>
  );
};

function EveryQuiz() {
  const navigate = useNavigate();

  //  layout
  return (
    <div className="home">
      <div style={{ minHeight: '20px', backgroundColor: '#a0e4fc'}} className="mb-0 w-100"></div>
      <Container fluid style={{backgroundColor: 'white', marginTop: '-20px', borderTopLeftRadius: '30px', borderTopRightRadius: '30px' }}>
        {/* Approfondimenti */}
        <Row className="mb-0 row-fade"> 
          <p className='quizzes-text'>Quizzes <FontAwesomeIcon className='insights-icon' icon={faSquarePollHorizontal}  style={{color: 'green'}} /></p>
        </Row>
        <Row style={{marginTop: '20px'}} className="mb-0 row-fade"> 
          <p className='quizzes-explain-text'>
            Benvenuto nella sezione per testare le tue conoscenze. Ti verrà fornito un caso clinico randomico tra quelli disponibili e ti verranno poste delle domande su diversi aspetti.
            <br></br>
            <br></br>
            Il punteggio finale viene calcolato come segue:
            <ul>
              <br></br>
              <li>31/(numero delle domande) dà il punteggio per ogni domanda corretta</li>
              <li>(31/(numero delle domande)) * 1.5 dà la penalità per ogni domanda errata</li>
            </ul>
            Al termine del test ti verrà fornito un resoconto riportante:
            <ul>
              <br></br>
              <li>La tua votazine finale espressa in 30esimi</li>
              <li>Le domande in cui la risposta fornita è errata, senza però indicare la risposta corretta</li>
            </ul>
            </p>
        </Row>
        <Row style={{ marginTop: '20px'}} className="mb-0 row-fade" justifyContent="center">
          <Col xs={12} sm={12} xl={12} className="d-flex justify-content-center">
            <button className="btn start-button" style={{ display: 'flex', alignItems: 'center' }}>
              Iniziamo 
              <FontAwesomeIcon className="start-button-icon" icon={faPlay} style={{ marginLeft: '5px'}} 
            />
        </button>
          </Col>
        </Row>
      </Container>

    </div>
  );
  
}

export default EveryQuiz;
