import React, { useState, useEffect, useMemo } from 'react';
import { Container, Navbar, Nav } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHome, faBook, faMagnifyingGlass, faClipboard, faAddressCard } from '@fortawesome/free-solid-svg-icons';
import "../../assets/HeaderStyles.css";
import { useNavigate, useLocation } from 'react-router-dom';

const NavBar = () => {
    const [activeItemIndex, setActiveItemIndex] = useState(0);
    const navigate = useNavigate();
    const location = useLocation();
    const currentRoute = location.pathname;

    const navItems = useMemo(() => [
        { icon: faHome, text: "Home", path: '/' },
        { icon: faBook, text: "Casi clinici", path: '/everyclinicalcase' },
        { icon: faMagnifyingGlass, text: "Approfondimenti", path: '/everyinsight' },
        { icon: faClipboard, text: "Quiz", path: '/everyquiz' },
        { icon: faAddressCard, text: "About", path: '/about' },
    ], []);

    useEffect(() => {
        const baseRoute = currentRoute.split('/')[1];
        let index;
    
        if (baseRoute === 'insight') {
            index = navItems.findIndex(item => item.path === `/everyinsight`);
        } else {
            index = navItems.findIndex(item => item.path === `/${baseRoute}`);
        }
        
        setActiveItemIndex(index);
        console.log(baseRoute);
    }, [navItems, currentRoute]);
    
    function handleOnClick(index) {
        setActiveItemIndex(index);
        if (navItems[index].path) {
            navigate(navItems[index].path);
        }
    }

    return (
        <Navbar expand="lg">
            <Container fluid>
                <Navbar.Brand href="/IDentKnow" className="me-auto d-flex align-items-center">
                    <img src={process.env.PUBLIC_URL + '/logo512.png'} width="30" height="30" alt="" />
                    <p className="mb-0 ms-2">i Den't Know</p>
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav" className="justify-content-end">
                    <Nav className="align-items-center items-container">
                        {navItems.map(({ icon, text }, index) => (
                            <Nav.Link
                                key={text}
                                onClick={() => handleOnClick(index)}
                                className={`d-flex align-items-center ${index === activeItemIndex ? "active" : ""}`}
                            >
                                <FontAwesomeIcon icon={icon} className='icon' />
                                <span className="ms-2">{text}</span>
                            </Nav.Link>
                        ))}
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    );
};

export default NavBar;
