import '../../assets/HomeStyles.css';
import PatientsData from '../../data/PatientsData';
import InsightsData from '../../data/InsightsData';
import ColorDecider from '../../utils/ColorDecider';
import React, { useState, useEffect} from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFire, faEye} from '@fortawesome/free-solid-svg-icons';
import { Container, Row, Col} from 'react-bootstrap';
import Card from 'react-bootstrap/Card';
import { useNavigate } from 'react-router-dom';
import ReactTyped from "react-typed";

const getHotCases = (maxCards = 4) => {
  let selectedCards = [];

  // Get at least one card from each category
  const categories = [...new Set(PatientsData.map(item => item.category))];
  categories.forEach(category => {
    const cardsFromCategory = PatientsData.filter(item => item.category === category);
    selectedCards.push(cardsFromCategory[Math.floor(Math.random() * cardsFromCategory.length)]);
  });

  // Shuffle the original array for random selection
  let shuffled = [...PatientsData].sort(() => 0.5 - Math.random());

  // Add random cards until we reach the maximum number of cards
  for (let item of shuffled) {
    if (!selectedCards.includes(item) && selectedCards.length < maxCards) {
      selectedCards.push(item);
    }
  }
  return selectedCards;
};

const selectedHotCasesList = getHotCases();

const getHotInsights = (maxCards = 4) => {
  // Shuffle the array
  const shuffled = [...InsightsData].sort(() => 0.5 - Math.random());

  // Select the first maxCards elements
  return shuffled.slice(0, maxCards);
};

const selectedHotInsightsList = getHotInsights();

const CaseCard = ({ data, onClick }) => {
  const [backgroundColor, setBackgroundColor] = useState('transparent');

  // Utility function to convert hex to RGBA
  function hexToRGBA(hex, opacity) {
      let r = parseInt(hex.slice(1, 3), 16),
          g = parseInt(hex.slice(3, 5), 16),
          b = parseInt(hex.slice(5, 7), 16);

      if (opacity > 1) opacity = opacity / 100;
      
      return `rgba(${r}, ${g}, ${b}, ${opacity})`;
  }

  useEffect(() => {
      const newBackgroundColor = hexToRGBA(ColorDecider(data.category), 0.2);
      const timeoutId = setTimeout(() => setBackgroundColor(newBackgroundColor), 300);

      return () => clearTimeout(timeoutId);
  }, [data.category]);

  

  return (
      <Card 
          onClick={onClick} 
          className='case-card' 
          style={{ backgroundColor: backgroundColor, transition: 'background-color 0.3s ease' }}
      >
          <Card.Body className="text-center case-card-body">
              <p className='case-card-title'>{data.title}</p>
              <div className='case-card-description'>{data.card.description}</div> {/* Render as React element */}
          </Card.Body>
          <p className='case-card-id'>Caso numero: {data.id}</p>
      </Card>
  );
};


//definisco il componente della carta approfondimento
const InsightCard = ({ data, onClick}) => {

  return (
    <Card onClick={onClick} className='insight-card d-flex flex-column'> {/* Use 'flex-column' to stack children vertically */}
      <Card.Img variant="top" src={process.env.PUBLIC_URL + data.cardImage} alt="Card image" />
      <Card.Body className="text-center insight-card-body">
        <p className='insight-card-title'>{data.title}</p>
      </Card.Body>
      <Card.Footer className='insight-card-footer'>
        <p className='insight-card-id'>Approfondimento {data.id}</p>
      </Card.Footer>
    </Card>
  );
};


function Home() {
  const navigate = useNavigate();

  function onCaseCardClick(cardIndex) {
    navigate('/everyclinicalcase/' + cardIndex);
  }

  function NavigatetoInsightClick(insightPage) {
    navigate('/insight/' + insightPage);
    setTimeout(() => {

          window.scroll({ top: -1, left: 0, behavior: "smooth" });
      
    }, 10); 
  }

  //  layout
  return (
    <div className="home">
      <div style={{ minHeight: '20px', backgroundColor: '#a0e4fc'}} className="mb-0 w-100"></div>
      <Container fluid style={{backgroundColor: 'white', marginTop: '-20px', borderTopLeftRadius: '30px', borderTopRightRadius: '30px' }}>
        {/* Slogan Section*/}
        <Row className="mb-0 justify-content-center text-center align-items-center slogan-row" >
          <ReactTyped backSpeed={20} className='slogan-text' strings={["Scopri ^500 quello che non sapevi di non sapere..."]} typeSpeed={60}/>
        </Row>
        {/* Hot cases */}
        <Row className="mb-0 row-fade"> 
          <p className='hot-cases-text'>Hot Cases <FontAwesomeIcon className='hot-cases-icon' icon={faFire}  style={{color: 'red'}} /></p>
        </Row>
        <Row className="mb-0 align-items-stretch hot-cases-row" style={{ minHeight: '200px' }}> {/* Use align-items-stretch to make cols the same height */}
          <Col className="align-items-center"lg={12}>
            <Row>
              {selectedHotCasesList.map((item, index) => (
                <Col xs={6} sm={6} md={4} lg={4} xl={2} className="mb-4 d-flex" key={index}> {/* Add d-flex to make the Col a flex container */}
                  <CaseCard 
                    data={item} 
                    className={`w-100`}
                    onClick={()=> onCaseCardClick(index)}
                  />
                </Col>
              ))}
            </Row>
          </Col>
        </Row>
        {/* Approfondimenti */}
        <Row className="mb-0 row-fade"> 
          <p className='insights-text'>Approfondimenti <FontAwesomeIcon className='insights-icon' icon={faEye}  style={{color: 'blue'}} /></p>
        </Row>
        <Row className="mb-0 align-items-stretch insights-row" style={{ minHeight: '200px' }}> {/* Use align-items-stretch to make cols the same height */}
          <Col lg={12}>
            <Row>
              {selectedHotInsightsList.map((card, index) => (
                <Col xs={6} sm={6} md={4} lg={3} xl={3} className="mb-4 d-flex" key={index}> {/* Adjusted md and lg to 3 to take up full width with 4 cards */}
                  <InsightCard
                    data={card} 
                    className={`w-100`}
                    onClick={()=> NavigatetoInsightClick(card.id)}
                  />
                </Col>
              ))}
            </Row>
          </Col>
        </Row>
      </Container>
    </div>
  );
  
}

export default Home;
