import '../../assets/EveryInsightStyles.css';
import InsightsData from '../../data/InsightsData';
import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEye } from '@fortawesome/free-solid-svg-icons';
import { Container, Row, Col } from 'react-bootstrap';
import Card from 'react-bootstrap/Card';
import { useNavigate } from 'react-router-dom';


//definisco il componente della carta approfondimento
const InsightCard = ({ data, onClick}) => {
  return (
    <Card onClick={onClick} className='insight-card'>
      <Card.Img variant="top" src={process.env.PUBLIC_URL + data.cardImage} alt="Card image" />
      <Card.Body className="text-center insight-card-body">
        <p className='insight-card-title'>{data.title}</p>
      </Card.Body>
      <Card.Footer className='insight-card-footer'>
        <p className='insight-card-id'>Approfondimento {data.id}</p>
      </Card.Footer>
    </Card>
  );
};

function EveryInsight() {
  const navigate = useNavigate();

  function NavigatetoInsightClick(insightPage) {
    navigate('/insight/' + insightPage);
    setTimeout(() => {

          window.scroll({ top: -1, left: 0, behavior: "smooth" });
      
    }, 10); 
  }

  //  layout
  return (
    <div className="home">
      <div style={{ minHeight: '20px', backgroundColor: '#a0e4fc'}}></div>
      <Container fluid style={{backgroundColor: 'white', marginTop: '-20px', borderTopLeftRadius: '30px', borderTopRightRadius: '30px' }}>
        <Row className="mb-0 row-fade"> 
          <p className='insights-text'>Approfondimenti <FontAwesomeIcon className='insights-icon' icon={faEye}  style={{color: 'blue'}} /></p>
        </Row>
        <Row className="mb-0 align-items-stretch insights-row" style={{ minHeight: '100px' }}> {/* Use align-items-stretch to make cols the same height */}
          <Col lg={12}>
            <Row>
              {InsightsData.map((card, index) => (
                <Col xs={6} sm={4} md={4} lg={2} xl={2} className="mb-4 d-flex" key={index}> {/* Adjusted md and lg to 3 to take up full width with 4 cards */}
                  <InsightCard
                    data={card} 
                    className={`w-100`}
                    onClick={()=> NavigatetoInsightClick(card.id)}
                  />
                </Col>
              ))}
            </Row>
          </Col>
        </Row>
      </Container>
    </div>
  );
  
}

export default EveryInsight;
