import React, { useState, useEffect } from 'react';
import { Worker, Viewer } from '@react-pdf-viewer/core';
import { Container } from 'react-bootstrap';
import '@react-pdf-viewer/core/lib/styles/index.css';
import { useParams } from 'react-router-dom';
import InsightsData from '../../data/InsightsData';

const pdfjsVersion = '3.11.174';

const PdfViewer = () => {
  const { id } = useParams();
  const [selectedCard, setSelectedCard] = useState(null);
  const [isLargeScreen, setIsLargeScreen] = useState(window.innerWidth > 991);

  useEffect(() => {
    const handleResize = () => {
      setIsLargeScreen(window.innerWidth > 991);
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  useEffect(() => {
    const card = InsightsData.find(card => card.id.toString() === id);
    setSelectedCard(card);
  }, [id]);

  if (!selectedCard) {
    return <div>Loading...</div>; // Or some other loading/error state
  }

  return (
    <>
      <Container fluid style={{paddingTop: '5%' }}>
        {isLargeScreen ? (
          <div style={{ height: '90%', width: '100%' }}>
            <Worker workerUrl={`https://unpkg.com/pdfjs-dist@${pdfjsVersion}/build/pdf.worker.min.js`}>
              <Viewer showToolbar={true} fileUrl={process.env.PUBLIC_URL + selectedCard.page.InsightPDF} />
            </Worker>
          </div>
        ) : (
          <img src={process.env.PUBLIC_URL + selectedCard.page.InsightImage} alt="Responsive" style={{ width: '100%', height: 'auto' }} />
        )}
      </Container>
    </>
  );
};

export default PdfViewer;
