const ColorDecider = (category) => {
    let circleColor;

    switch (category) {
        case 'Tutti':
            circleColor = '#ffa500';
            break;
        case 'Restaurativa':
            circleColor = '#FFBF00';
            break;
        case 'Chirurgia orale':
            circleColor = '#50e0ff';
            break;
        case 'Protesi':
            circleColor = '#50C878';
            break;
        case 'Patologia orale':
            circleColor = '#ff6666';
            break;
        case 'Urgenze':
                circleColor = '#ff66b2';
                break;   
        case 'Gnatologia':
                circleColor = '#CEFE9E';
                break;   
        case 'Parodontologia':
                circleColor = '#B869FF';
                break;  
        case 'Pedodonzia':
                circleColor = '#F687FF';
                break;  
        default:
            circleColor = '#B0B0B0'; // Error handling
    }
    return circleColor;
};

export default ColorDecider;
